.third {
  background-image: url('../../../public/second_background.jpg');
  background-attachment: fixed;
  // background-position-x: 30rem;

  .container {
    display: flex;
    flex-direction: column;
    border: 0.5rem solid #000;
    background-color: #fff;

    .div_title {
      margin-bottom: 2rem;
      font-size: 2.9rem;
      text-align: left;
    }

    >div {
      flex: 1;
      padding: 3.5rem;
    }

    .top {
      // border-bottom: 0.1rem solid #ddd;

      .title_box {
        margin-bottom: 3.5rem;
      }


      .info_desc {
        display: flex;
        gap: 2.5rem;

        >div {
          flex: 1;
          height: 31rem;

          &.left {
            .tech_list {
              @for $i from 1 through 4 {
                .itm0#{$i} {
                  background-image: url('../../../public/tech_logo_0#{$i}.png');
                  background-size: contain;
                }
              }
            }
          }

          &.right {
            .tech_list {
              @for $i from 1 through 5 {
                .itm0#{$i} {
                  background-image: url('../../../public/tech_logo_0#{$i}.png');
                  background-size: contain;
                }
              }
            }
          }

          .sub_desc {
            display: flex;
            flex-wrap: wrap;
            gap: 1.5rem;

            >li {
              display: flex;
              flex-direction: column;
              width: 30rem;

              &:last-child {
                width: 50rem;
              }

              strong {
                display: block;
                margin-bottom: 1rem;
                font-size: 1.6rem;
              }

              span {
                display: block;
                font-size: 1.3rem;
              }

              .link_list {
                display: flex;
                gap: 1.5rem;

                >li {
                  font-size: 1.3rem;
                  font-weight: 400;

                  >a {
                    font-size: 1.3rem;
                    text-decoration: none;
                    color: #333;
                  }
                }
              }

              .tech_list {
                display: flex;
                justify-content: flex-start;
                gap: 2.5rem;
                width: 60rem;
                margin-left: -1rem;

                >li {
                  width: 10rem;

                  figure {
                    // outline: 0.1rem solid #ddd;
                    margin: 0 auto .5rem auto;
                    padding: 3rem;
                    width: 5rem;
                    height: 5rem;
                  }

                  span {
                    text-align: center;
                    font-weight: 500;
                  }
                }
              }
            }
          }

          &.left {
            position: relative;

            &::after {
              content: '';
              position: absolute;
              top: -10.5rem;
              right: -3%;
              width: 0.1rem;
              height: 79rem;
              background-color: #ddd;
            }
          }

          &.right {
            margin-left: 1.5rem;
          }

          .desc {
            h5 {
              margin-bottom: 2.5rem;
              font-size: 2.4rem;
              font-weight: 500;
            }

            .sub_info {
              margin-bottom: 3rem;

              strong {
                display: block;
                margin-bottom: 1rem;
                font-size: 1.9rem;
              }

              p {
                font-size: 1.5rem;
                line-height: 1.4;
              }


            }


          }
        }
      }
    }

    .bottom {
      display: flex;
      gap: 2.5rem;

      >a {
        flex: 1;

        >figure {
          width: 100%;
          height: 100%;

          &.left {
            background-image: url('../../../public/homepage_02.png');
            background-position-y: 0;
            transition: 5s;

            &:hover {
              background-position-y: 100%;
            }
          }

          &.right {
            background-image: url('../../../public/homepage.png');
            background-position-y: 0;
            transition: 5s;

            &:hover {
              background-position-y: 100%;
            }
          }
        }

      }
    }
  }
}