@keyframes autoOpacity {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

.first {
  background-image: url('../../../public/first_background.jpg');
  background-size: cover;

  .top {
    position: relative;
    border-bottom: 0.1rem solid #aaa;
    height: 50vh;
    transition: 1s;

    &:hover {
      background-color: #111;
      color: #fff;

      .float_box::after {
        background-color: #fff;
      }
    }

    .float_box {
      position: absolute;
      top: 4%;
      left: 1.5%;

      &::after {
        content: '';
        position: absolute;
        top: 0;
        left: -4%;
        width: 1%;
        height: 100%;
        background-color: #333;
        transition: 0.5s;
      }

      p {
        font-size: 1.6rem;
        font-weight: 700;
        line-height: 1.3;
      }
    }

    .top_title {
      position: absolute;
      left: 50%;
      bottom: 1rem;
      transform: translateX(-50%);

      .main_title {
        width: 110rem;

        >div {

          p {
            font-size: 4.5rem;
            font-weight: 300;
            line-height: 1.4;
            text-align: left;
          }

          strong {
            font-size: 4.5rem;
          }
        }
      }
    }
  }

  .bottom {
    height: 50vh;
    position: relative;
    transition: 0.5s;

    &:hover {
      background-color: #111;
      color: #fff;
    }

    .bottom_title {
      position: absolute;
      top: 1rem;
      left: 50%;
      transform: translateX(-50%);

      .main_title {
        width: 110rem;

        p {
          font-size: 4.5rem;
          font-weight: 300;
          line-height: 1.4;
          text-align: left;
        }

        strong {
          font-size: 4.5rem;
        }
      }
    }

    .deco {
      position: absolute;
      bottom: 4%;
      right: 1.5%;
      font-size: 2.5rem;
    }
  }
}