.fourth {
  background-image: url('../../../public/second_background.jpg');
  background-attachment: fixed;

  .container {
    border: 0.5rem solid #000;
    padding: 3.5rem;
    background-color: #fff;

    .grid_box {
      display: grid;
      grid-template-columns: repeat(3, 45rem);
      gap: 3.5rem;

      height: 100%;

      >div {
        height: 34.2rem;

        &.intro {
          .div_title {
            margin-bottom: 4rem;
            font-size: 2rem;
          }

          .info_desc {
            margin-bottom: 2rem;

            strong {
              display: block;
              margin-bottom: 1rem;
              font-size: 1.5rem;
            }

            p {
              font-size: 1.4rem;
              line-height: 1.313;
            }
          }

          .desc_list {
            display: flex;
            flex-direction: column;
            gap: 2rem;


            >li:last-child {
              strong {
                margin-bottom: 1rem;
              }
            }

            >li {
              display: flex;
              flex-direction: column;

              >strong {
                display: block;
                margin-bottom: .5rem;
                font-size: 1.6rem;
              }

              span {
                display: block;
                font-size: 1.3rem;
              }

              .sub_list {
                display: flex;
                gap: 1.5rem;

                @for $i from 1 through 5 {
                  .itm0#{$i} {
                    background-image: url('../../../public/toy_tech_0#{$i}.png');
                    background-size: contain;
                  }
                }

                >li {
                  width: 10rem;

                  figure {
                    // outline: 1px solid #000;
                    margin-bottom: .5rem;
                    width: 100%;
                    height: 7rem;
                  }

                  strong {
                    display: block;
                    text-align: center;
                  }
                }

              }
            }
          }
        }

        &.toy {

          @for $i from 1 through 5 {
            .itm0#{$i} {
              background-image: url('../../../public/toy_bg_0#{$i}.png');
              background-position-y: 0;
              transition: 5s;

              &:hover {
                background-position-y: 100%;
              }
            }
          }

          >a {
            // outline: 1px solid #000;
            display: block;
            margin-bottom: 1rem;
            height: 30rem;

            figure {
              // outline: 1px solid #000;
              height: 100%;
            }
          }


          .desc_box {
            text-align: center;

            h4 {
              margin-bottom: .5rem;
              font-size: 1.5rem;
            }

            a {
              display: block;
              font-size: 1.6rem;
              font-weight: 500;
              text-decoration: none;
              color: #333;
            }
          }
        }
      }
    }

  }
}